import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import BondingCurve from './bonding-curves-class';
import { Container, TextField, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Typography, Box } from '@mui/material';

function App() {
  const { register, handleSubmit, reset } = useForm({
    defaultValues: {
      initialPrice: 1.00,
      slope: 0.1,
      buyFeePercent: 0.00,
      sellFeePercent: 0.00
    }
  });

  const { register: registerBuy, handleSubmit: handleSubmitBuy, reset: resetBuy } = useForm({
    defaultValues: {
      amount: 1
    }
  });

  const { register: registerSell, handleSubmit: handleSubmitSell, reset: resetSell } = useForm({
    defaultValues: {
      amount: 1
    }
  });

  const [bondingCurve, setBondingCurve] = useState(null);
  const [transactions, setTransactions] = useState([]);

  const onSubmit = data => {
    const { initialPrice, slope, buyFeePercent, sellFeePercent } = data;
    const newBondingCurve = new BondingCurve(
      initialPrice,
      slope,
      buyFeePercent,
      sellFeePercent
    );
    setBondingCurve(newBondingCurve);
    setTransactions([]); // Очищаем таблицу операций
  };

  const onBuy = data => {
    const { amount } = data;
    const amountNumber = Number(amount);
    console.log("Buying amount:", amountNumber); // Отладка
    if (bondingCurve) {
      const cost = bondingCurve.buyTokens(amountNumber);
      const price = cost / amountNumber;
      const updatedBondingCurve = Object.assign(Object.create(Object.getPrototypeOf(bondingCurve)), bondingCurve);
      setBondingCurve(updatedBondingCurve); // Обновляем состояние
      setTransactions([...transactions, {
        operation: 'buy',
        amount: amountNumber,
        price: Number(price.toFixed(2)),
        total: cost,
        totalSupply: updatedBondingCurve.totalSupply,
        totalUSDT: updatedBondingCurve.totalUSDTSpent - updatedBondingCurve.totalUSDTReceived,
      }]);
      resetBuy({ amount: 1 }); // Сбросить значение поля после покупки
    }
  };

  const onSell = data => {
    const { amount } = data;
    const amountNumber = Number(amount);
    console.log("Selling amount:", amountNumber); // Отладка
    if (bondingCurve) {
      const revenue = bondingCurve.sellTokens(amountNumber);
      const price = revenue / amountNumber;
      const updatedBondingCurve = Object.assign(Object.create(Object.getPrototypeOf(bondingCurve)), bondingCurve);
      setBondingCurve(updatedBondingCurve); // Обновляем состояние
      setTransactions([...transactions, {
        operation: 'sell',
        amount: amountNumber,
        price: Number(price.toFixed(2)),
        total: revenue,
        totalSupply: updatedBondingCurve.totalSupply,
        totalUSDT: updatedBondingCurve.totalUSDTSpent - updatedBondingCurve.totalUSDTReceived,
      }]);
      resetSell({ amount: 1 }); // Сбросить значение поля после продажи
    }
  };

  const formatNumber = num => Number(parseFloat(num).toFixed(2));

  return (
    <Container>
      <Typography variant="h4" gutterBottom>$MOSKVICHЁV</Typography>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box mb={2}>
          <TextField
            label="Начальная цена"
            type="number"
            step="0.01"
            inputProps={{ step: "0.01" }}
            {...register('initialPrice', { valueAsNumber: true })}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Угол наклона"
            type="number"
            step="0.01"
            inputProps={{ step: "0.01" }}
            {...register('slope', { valueAsNumber: true })}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Процент комиссии за покупку"
            type="number"
            step="0.01"
            inputProps={{ step: "0.01" }}
            {...register('buyFeePercent', { valueAsNumber: true })}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Процент комиссии за продажу"
            type="number"
            step="0.01"
            inputProps={{ step: "0.01" }}
            {...register('sellFeePercent', { valueAsNumber: true })}
            fullWidth
            margin="normal"
          />
          <Button type="submit" variant="contained" color="primary">Инициализация</Button>
        </Box>
      </form>
      {bondingCurve && (
        <Box mt={4}>
          <Typography variant="h6">Текущая цена токена: {formatNumber(bondingCurve.getPrice(bondingCurve.totalSupply))}</Typography>
          <Typography variant="h6">Количество купленных токенов: {formatNumber(bondingCurve.totalSupply)}</Typography>
          <Typography variant="h6">Общее количество USDT, потраченное на покупку: {formatNumber(bondingCurve.getUSDTBalance().totalUSDTSpent)}</Typography>
          <Typography variant="h6">Общее количество USDT, полученное от продажи: {formatNumber(bondingCurve.getUSDTBalance().totalUSDTReceived)}</Typography>
          <Typography variant="h6">Нетто USDT: {formatNumber(bondingCurve.getUSDTBalance().netUSDT)}</Typography>
          <form onSubmit={handleSubmitBuy(onBuy)}>
            <Box mb={2}>
              <TextField
                label="Количество"
                type="number"
                step="0.01"
                inputProps={{ step: "0.01" }}
                {...registerBuy('amount', { valueAsNumber: true })}
                fullWidth
                margin="normal"
              />
              <Button type="submit" variant="contained" color="primary">Покупка</Button>
            </Box>
          </form>
          <form onSubmit={handleSubmitSell(onSell)}>
            <Box mb={2}>
              <TextField
                label="Количество"
                type="number"
                step="0.01"
                inputProps={{ step: "0.01" }}
                {...registerSell('amount', { valueAsNumber: true })}
                fullWidth
                margin="normal"
              />
              <Button type="submit" variant="contained" color="secondary">Продажа</Button>
            </Box>
          </form>
          <Typography variant="h6" gutterBottom>Таблица операций</Typography>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Операция</TableCell>
                  <TableCell>Количество</TableCell>
                  <TableCell>Цена</TableCell>
                  <TableCell>Сумма, USDT</TableCell>
                  <TableCell>Общее количество</TableCell>
                  <TableCell>Итого, USDT</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {transactions.map((transaction, index) => (
                  <TableRow key={index}>
                    <TableCell>{transaction.operation}</TableCell>
                    <TableCell>{formatNumber(transaction.amount)}</TableCell>
                    <TableCell>{formatNumber(transaction.price)}</TableCell>
                    <TableCell>{formatNumber(transaction.total)}</TableCell>
                    <TableCell>{formatNumber(transaction.totalSupply)}</TableCell>
                    <TableCell>{formatNumber(transaction.totalUSDT)}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      )}
    </Container>
  );
}

export default App;