class BondingCurve {
    constructor(initialPrice, slope, buyFeePercent = 0, sellFeePercent = 0) {
        this.initialPrice = initialPrice; // Начальная цена
        this.slope = slope; // Угол наклона
        this.totalSupply = 0; // Общее количество токенов
        this.totalBought = 0; // Общее количество купленных токенов
        this.totalSold = 0; // Общее количество проданных токенов
        this.totalUSDTSpent = 0; // Общее количество USDT, потраченное на покупку токенов
        this.totalUSDTReceived = 0; // Общее количество USDT, полученное от продажи токенов
        this.buyFeePercent = buyFeePercent; // Процент комиссии при покупке
        this.sellFeePercent = sellFeePercent; // Процент комиссии при продаже
        this.priceHistory = []; // История цен для графика
    }

    // Метод для расчета цены на основе общего количества токенов
    getPrice(supply) {
        return this.initialPrice + this.slope * supply; // Цена = начальная цена + угол наклона * количество
    }

    // Метод для покупки токенов
    buyTokens(amount) {
        let totalCost = 0;
        for (let i = 0; i < amount; i++) {
            totalCost += this.getPrice(this.totalSupply + i);
        }
        const fee = totalCost * (this.buyFeePercent / 100);
        totalCost += fee; // Добавляем комиссию к общей стоимости
        this.totalSupply += amount; // Увеличиваем общее количество токенов
        this.totalBought += amount; // Увеличиваем общее количество купленных токенов
        this.totalUSDTSpent += totalCost; // Увеличиваем общее количество USDT, потраченное на покупку
        this.priceHistory.push({ volume: this.totalBought, price: this.getPrice(this.totalSupply) }); // Добавляем в историю цен
        return totalCost; // Возвращаем стоимость покупки с учетом комиссии
    }

    // Метод для продажи токенов
    sellTokens(amount) {
        let totalRevenue = 0;
        for (let i = 0; i < amount; i++) {
            totalRevenue += this.getPrice(this.totalSupply - i - 1);
        }
        const fee = totalRevenue * (this.sellFeePercent / 100);
        totalRevenue -= fee; // Вычитаем комиссию из общего дохода
        this.totalSupply -= amount; // Уменьшаем общее количество токенов
        this.totalSold += amount; // Увеличиваем общее количество проданных токенов
        this.totalUSDTReceived += totalRevenue; // Увеличиваем общее количество USDT, полученное от продажи
        this.priceHistory.push({ volume: this.totalBought, price: this.getPrice(this.totalSupply) }); // Добавляем в историю цен
        return totalRevenue; // Возвращаем доход от продажи с учетом комиссии
    }

    // Метод для отображения количества купленных токенов за вычетом проданных и их стоимости по текущему курсу
    getNetTokensAndValue() {
        const netTokens = this.totalBought - this.totalSold;
        const currentValue = netTokens * this.getPrice(this.totalSupply);
        return { netTokens, currentValue };
    }

    // Метод для отобр��жения общего количества USDT, потраченного и полученного
    getUSDTBalance() {
        const netUSDT = this.totalUSDTSpent - this.totalUSDTReceived;
        return { totalUSDTSpent: this.totalUSDTSpent, totalUSDTReceived: this.totalUSDTReceived, netUSDT };
    }
}

export default BondingCurve;